(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('UpdatePasswordController', UpdatePasswordController);

  /** @ngInject */
  function UpdatePasswordController($state, $stateParams, $log, UserService, UserApi, toastr, utils, $timeout) {
    var vm = this, 
        user = UserService.getUser(),
        oPwd = user.password, 
        loginPattern = /^(?!\d+$|[a-zA-Z]+$|[\W-_]+$)[\s\S]{6,16}$/,
        payPattern = /[\s\S]{6,16}/;

    vm.isLogin = $stateParams.type === 'login';
    vm.typeStr = vm.isLogin ? '登录' : '支付';

    vm.user = {};

    vm.checkOriginal = checkOriginal;
    vm.checkNew = checkNew;
    vm.checkConfirm = checkConfirm;
    vm.submit = submit;

    // only for login password
    function checkOriginal() {
      if(vm.isLogin && vm.user.oriPassword && (vm.user.oriPassword !== oPwd)) {
        toastr.info('原密码输入错误!');
        vm.oMismatch = true;
      }
    }

    function checkNew() {
      var pattern = vm.isLogin ? loginPattern : payPattern;
      if(vm.user.newPassword && !pattern.test(vm.user.newPassword)) {
        toastr.info('新密码不符合规则！');
        vm.nInvalid = true;
      }
    }

    function checkConfirm() {
      if(vm.user.confirm && (vm.user.newPassword !== vm.user.confirm)) {
        toastr.info('两次输入密码不一致！');
        vm.nMismatch = true;
      }
    }

    function submit() {
      vm.isLogin ? updateLoginPassword() : updatePayPassword();
    }

    function updateLoginPassword() {
      UserApi.changePassword(vm.user).success(function(res) {
        if(res.flag === 4) {
          toastr.info('登录密码修改成功！');
          user.password = vm.user.newPassword;
          UserService.setUser(user);
          $timeout(utils.goBack, 1000);
        } else {
          toastr.info(res.msg);
        }
      });
    }

    function updatePayPassword() {
      UserApi.changePayPassword(vm.user).success(function(res) {
        if(res.flag === 1) {
          toastr.info('支付密码修改成功！');
          $timeout(utils.goBack, 1000);
        } else if(res.flag === 3) {
          toastr.info('原支付密码输入错误!');
          vm.oMismatch = true;
        } else {
          toastr.info(res.msg);
        }
      });
    }

  }
})();

