(function() {
  'use strict';

  angular
    .module('nonoApp')
    .directive('investCounter', investCounter);

  /** @ngInject */
  function investCounter($log, $filter) {
    var directive = {
      restrict: 'E',
      required: 'ngModel',
      scope: {
        plan: '=',
        order: '=ngModel'
      },
      templateUrl: 'app/components/invest-counter/invest.counter.html',
      link: function(scope, element, attrs) {
        var plan = scope.plan,
            min = plan.fp_price_min,
            peer = plan.fp_price_increment,
            max = Math.min(plan.balance, plan.fp_price_max),
            rate = plan.fp_rate_max,
            unit = plan.fp_expect_unit,
            expect = plan.fp_expect,
            minCount = min / peer,
            maxCount = max / peer;

        scope.order.earnings = 0;
        scope.order.count = 0;
        scope.order.price = 0;
        scope.max = max;
        

        scope.minus = function() {
          if(scope.order.count > minCount) {
            scope.order.count--;
          }
        };

        scope.plus = function() {
          if(scope.order.count < maxCount) {
            scope.order.count++;
          }
        };

        scope.$watch('order.count', function(count) {
          scope.order.price = count * peer;
        });

        scope.$watch('order.price', function(price) {
          if(unit == 1){ // day
            scope.order.earnings =  $filter('number')(price * (rate/100) / 365 * expect, 2);
          } else { // month
            scope.order.earnings =  $filter('number')(price * (rate/100) / 12 * expect, 2);
          }

          if(price > max) {
            scope.order.price = max;
          } else if(price < min) {
            scope.order.price = min;
          }
        });

        function init() {
          scope.order.count = minCount;
        }

        init();


      }
    };

    return directive;
  }

})();

