(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('WelfareController', WelfareController);

  /** @ngInject */
  function WelfareController($rootScope, $scope, WelfareApi, UserService) {
    var vm = this,
      slider,
      accountInfo = UserService.getAccountInfo(),
      couponPageIndex,
      interestPageIndex,
      coinPageIndex;

    vm.level = accountInfo.level || 0;
    vm.coin = accountInfo.coin || 0;

    vm.sliderTabs = ['现金券', '加息券', '特权本金', '诺币', '现金红包'];

    vm.sliderIndex = 0;

    vm.couponList = [];
    vm.interestList = [];
    vm.coinLogList = [];

    vm.loadMore = loadMore;

    $scope.$watch(function() {
      return vm.sliderIndex;
    }, function(val) {
      slider && slider.slideTo && slider.slideTo(+val);
    });

    $scope.$on("$ionicSlides.sliderInitialized", function(event, data) {
      // data.slider is the instance of Swiper
      slider = data.slider;
    });

    $scope.$on("$ionicSlides.slideChangeEnd", function(event, data) {
      // note: the indexes are 0-based
      vm.sliderIndex = data.slider.activeIndex;
      $scope.$apply();
    });

    init();

    function init() {
      couponPageIndex = 0;
      interestPageIndex = 0;
      coinPageIndex = 0;

      vm.couponList.length = 0;
      vm.interestList.length = 0;
      vm.coinLogList.length = 0;

      loadCouponList();
      loadInterestList();
      loadPPList();
      loadPP();
      loadCoinList();
      loadRP();
    }

    function loadMore(type) {
      switch(type) {
        case 'coupon':
          loadCouponList();
          break;
        case 'interest':
          loadInterestList();
          break;
        case 'coin':
          loadCoinList();
          break;
      }
    }

    // 现金券
    function loadCouponList() {
      WelfareApi.getWelfares({
        type: 'coupon',
        pageIndex: couponPageIndex,
        itemsPerPage: 10
      }).success(function(res) {
        if (res.flag === 1) {
          res.data && res.data.content && res.data.content.forEach(function(_item) {
            vm.couponList.push(_item);
          });

          vm.hasMoreCoupon = res.data && res.data.content && res.data.content.length === 10;
        } else {
          vm.hasMoreCoupon = false;
        }
      }).finally(function() {
        // $rootScope.$broadcast('scroll.refreshComplete');
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      couponPageIndex++;
    }

    // 加息券
    function loadInterestList() {
      WelfareApi.getWelfares({
        type: 'interest',
        pageIndex: interestPageIndex,
        itemsPerPage: 10
      }).success(function(res) {
        if (res.flag === 1) {
          res.data && res.data.content && res.data.content.forEach(function(_item) {
            vm.interestList.push(_item);
          });

          vm.hasMoreInterest = res.data && res.data.content && res.data.content.length === 10;
        } else {
          vm.hasMoreInterest = false;
        }
      }).finally(function() {
        // $rootScope.$broadcast('scroll.refreshComplete');
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      interestPageIndex++;
    }

    // 特权本金
    function loadPP() {
      WelfareApi.getPrivilegePrincipal().success(function(res) {
        if(res.flag === 1) {
          vm.ppInfo = res.data;
        }
      });
    }

    // 特权本金列表
    function loadPPList() {
      WelfareApi.getPrivilegePrincipalLog().success(function(res) {
        if(res.flag === 1) {
          vm.ppList = res.data;
        }
      })
    }

    // 诺币记录
    function loadCoinList() {
      WelfareApi.coinLog({
        pageIndex: coinPageIndex,
        itemsPerPage: 10
      }).success(function(res) {
        if(res.flag === 1) {
          vm.coinLogCount = res.data.totalCount;

          res.data.result.forEach(function(_item) {
            var item = {
              name: _item.remark,
              amount: _item.ncay_op + (+_item.expend_coin || +_item.income_coin),
              date: _item.op_time
            };

            vm.coinLogList.push(item);
          });

          vm.hasMoreCoin = vm.coinLogList.length !== +vm.coinLogCount;
        }
      }).finally(function() {
        // $rootScope.$broadcast('scroll.refreshComplete');
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      coinPageIndex++;
    }

    // 现金红包
    function loadRP() {
      WelfareApi.getRedPackets().success(function(res) {
        if(res.flag === 1) {
          vm.redPackets = res.data;
        }
      });
    }
    
  }
})();

