(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('MallController', MallController);

  /** @ngInject */
  function MallController($state, $scope, $stateParams, SystemApi, $timeout, $ionicSlideBoxDelegate, user, UserService, UserApi, utils, $log, localStorageService) {
    var vm = this;

    vm.info = {};
    vm.tabs = [];

    vm.bannerLists = [];
    vm.nonoCoins = [];
    vm.coupons = [];
    vm.lottys = [];
    vm.select = select;
    vm.bannerSelect = bannerSelect;

    vm.select = select;
    vm.goDetail = goDetail;
    vm.goDetailList = goDetailList;

    init();

    $scope.$on('$ionicView.afterEnter', function() {
      $ionicSlideBoxDelegate.update();
    });

    function init() {
      var _user = UserService.getUser();
      if (!_user) {
        if (user.sessionId) {
          UserApi.getUserInfo().success(function(res) {
            if (res.flag == 1) {
              var data = res.data;
              var info = {};
              info.session_id = user.sessionId;
              info.realname = data.realName;
              info.mall = data.userLevel;
              info.mobilenum = data.mobileNum;
              UserService.setUser(info);
              info.mobilenum = utils.formatPhone(info.mobilenum);
              vm.info.showName = info.realname || info.mobilenum || '';
            }
          });
        }
      } else {
        _user.phone = utils.formatPhone(_user.phone);
        vm.info.showName = _user.realname || _user.phone || '';
      }
      SystemApi.getLevel().then(function(res) {

        vm.tabs = res.data;
        console.log(vm.tabs);
        vm.test = res.status;
        vm.info.number = parseInt($stateParams.number) || 0;
        if (vm.info.number > 3 || vm.info.number < 0) {
          vm.info.number = 0;
        }
        initIcon();
        select(0);
      });

      SystemApi.getBannerDetail().success(function(res) {
        if (+res.flag === 1) {
          var data = res.data;
          vm.bannerList = data.storeBanner;
          vm.nonoCoins = data.nonoCoin;
          vm.coupons = data.coupon;
          vm.lottys = data.lotty;

          vm.coupons.push({more: true});
          vm.nonoCoins.push({more: true});
          vm.lottys.push({more: true});
        }
      });

    }

    function goDetailList(index, type) {
      var curData = vm.nonoCoins[index];
      // $log.debug(index);
      localStorageService.set('mall.product.detailList', curData);
      $state.go('mall:product:list');
    }

    function goDetail(index, type) {
      var curData = vm.nonoCoins[index];
      // $log.debug(index);
      localStorageService.set('mall.product.detail', curData);
      $state.go('mall:exchange');
    }

    function initIcon() {
      angular.forEach(vm.tabs, function(tab) {
        var levelArr = tab.level;
        var len = levelArr.length;
        tab.grayIcon = true;
        for (var i = 0; i < len; i++) {
          if (vm.info.number == levelArr[i]) {
            tab.grayIcon = false;
            return;
          }
        }
      });
    }

    function select(index) {
      angular.forEach(vm.tabs, function(tab) {
        tab.active = false;
      });

      var tab = vm.tabs[index];
      tab.active = true;
      vm.detail = tab['detail'];

      // vm.slideLeft = true;

      // $timeout(function() {
      //   vm.slideLeft = false;
      // }, 500);
    }

    function bannerSelect(index) {
      // alert(index);
      $state.go('external', {
        name: '诺诺镑客',
        link: vm.bannerLists[index].link
      });
    }
  }
})();

