(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('HomeController', HomeController);

  /** @ngInject */
  function HomeController($scope, $rootScope, $state, user, AppService, BasicApi, $interval, $ionicScrollDelegate, $log, $location, localStorageService) {
    var vm = this, slider, ticker;

    // slider
    vm.options = {
      loop: true,
      effect: 'fade',
      speed: 500,
      autoplay: 2000
    };

    vm.hideNavBar = true;

    vm.doRefresh = loadData;
    vm.bannerDetail = bannerDetail;
    vm.hotDetail = hotDetail;
    vm.rapidDetail = rapidDetail;

    // banner sliders
    $scope.$on("$ionicSlides.sliderInitialized", function(event, data){
      // data.slider is the instance of Swiper
      slider = data.slider;
      triggerTicker();
    });

    $scope.$on('$ionicView.afterLeave', function(event, data) {
      ticker && $interval.cancel(ticker);
    });

    $scope.$on('$ionicView.beforeEnter', function(event, data) {
      slider && triggerTicker();
    });

    // main scroll 
    $scope.$watch(function() {
      return $ionicScrollDelegate.$getByHandle('mainScroll').getScrollPosition().top;
    }, function(val) {
      vm.hideNavBar = val < 44;
    });

    init();

    function triggerTicker() {
      if(!slider) return;

      ticker = $interval(function() {
        if(slider.previousIndex === (slider.slides && (slider.slides.length - 1))) {
          slider.slideTo(0);
        } else {
          slider.slideNext();
        }
      }, 3000);
    }

    function init(){
      var data = localStorageService.get('home');
      if(data) {
        initData(data);
      }
      // refresh data
      loadData();
    }

    function initData(data) {
      vm.banners = data.banner;
      vm.hotRecommend = data.hotRecommend;
      vm.characterInvest = data.characterInvest;
      vm.rapidInvest = data.rapidInvest;
    }

    function loadData() {
      BasicApi.getHomeData().success(function(res){
        if(res.flag == 1){
          var data = res.data;

          localStorageService.set('home', data);

          initData(data);
        }
      }).finally(function() {
        $rootScope.$broadcast('scroll.refreshComplete');
      });
    }

    function bannerDetail(index) {
      var item = vm.banners[index];

      linkJumper(item);
    }

    function hotDetail(index) {
      var p = vm.hotRecommend[index];
      var type = '';
      if(p.suffix.indexOf('新客') > -1){
        type = 'newbie'
      } else {
        if((p.expectUnit == '0' && p.expect<=12) || p.expectUnit == '1'){
          type = 'short';
        } else {
          type = 'long';
        }
      }
      $state.go('invest:detail', {type: type, id: p.fp_id});
    }

    function rapidDetail(index) {
      var item = vm.rapidInvest[index];
      $state.go('invest:list', {type: 'rapid' + (index + 1)});
    }

    function linkJumper(item) {
      $state.go('external', {name: item.name, link: item.link});
    }

  }
})();

