(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('AddressController', AddressController);

  /** @ngInject */
  function AddressController($scope, toastr, utils, UserApi, AddressService, UserService, $log, $timeout) {
    var vm = this;

    vm.provinceList = AddressService.provinceList;
    vm.cityList = AddressService.cityList;
    vm.cityList.length = 0; // fix vm.info.city = null

    vm.save = save;

    init();

    function init() {
      UserApi.shippingAddr().success(function(res) {
        if(res.flag === 1) {
          var data = res.data;
          vm.info = {
            province: {
              id: data.current_province_code,
              name: data.cur_province
            },
            city: {
              id: data.current_city_code,
              name: data.current_city
            },
            detail: data.cur_address
          };

          addListener();
        }
      });
    }

    function addListener() {
      $scope.$watch(function() {
        return vm.info.province;
      }, function(val, old) {
        if(val) {
          AddressService.selectProvince(val); 
          if(old && !angular.equals(val, old)) {
            vm.info.city = null;
          }
        }
      }, true);
    }

    function save() {
      UserApi.shippingAddr(vm.info).success(function(res) {
        if(res.flag === 1) {
          toastr.info('地址更新成功~');
          $timeout(utils.goBack, 1000);
        } else {
          toastr.info(res.msg);
        }
      });
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('ActivityController', ActivityController);

  /** @ngInject */
  function ActivityController($rootScope, $state, $stateParams, $ionicTabsDelegate, $ionicHistory, utils, BasicApi, BridgeService) {
    var vm = this;

    vm.select = select;
    vm.goBack = goBack;

    init();

    function init() {
      BasicApi.getActivity().success(function(res) {
        if(res.flag == 1) {
          vm.items = res.data;
        }
      });
    }

    function select(index) {
      var selectItem = vm.items[index];
      
      if(BridgeService.bridge) {
        BridgeService.send({
          type:'activity',
          data:{
            name: selectItem.title,
            link: selectItem.link
          }
        });
      } else {
        $state.go('external', {
          name: selectItem.title,
          link: selectItem.link
        });
      }
    }

    function goBack() {
      if($stateParams.from === 'home') {
        $ionicTabsDelegate.select(0);
      } else {
        $rootScope.$ionicGoBack();
      }
    }
    
  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $state, $ionicLoading, $http, $httpParamSerializerJQLike, $ionicHistory, BridgeService, UserService, AppService, BILog, utils, user, $location, $timeout, toastr, LIVE_LINK) {
    $rootScope.$on('loading:show', function () {
      $ionicLoading.show({
        noBackdrop: true,
        hideOnStateChange: true
      });
    });

    $rootScope.$on('loading:hide', function () {
      $ionicLoading.hide();
    });

    $rootScope.$on('ajax:error', function (evt, rejection) {
      if(rejection.config.method === 'POST') {
        toastr.info(rejection.status + ' : ' + rejection.statusText);
      }
    });

    // serialize http request data
    $http.defaults.transformRequest.unshift($httpParamSerializerJQLike);

    // override ionicGoBack
    $rootScope.$ionicGoBack = function () {
      if ($ionicHistory.backView()) {
        $ionicHistory.goBack();
      } else if (BridgeService.bridge) {
        BridgeService.backToApp();
      }
    };

    // routing interceptor
    $rootScope.$on('$stateChangeStart', function (evt, toState, toParams, fromState, fromParams) {
      switch(toState.name) {
        case 'talk':
          evt.preventDefault();
          // if(new Date().getTime() < 1472362200000) { // 2016-08-28 13:30:00
          //    $state.go('msg:detail', {
          //      type: 0,
          //      id: 1822
          //    });
          //  } else {
          $state.go('external', {
            name: '直播',
            link: LIVE_LINK + '&sessionId=' + user.sessionId,
          });
          // }
          break;
        case 'flow':
          evt.preventDefault();
          $state.go('error');
          break;
        case 'mine':
        case 'envoy':
        case 'limit':
        case 'welfare': // 福利
        case 'purchase': // 支付
          if(!user.sessionId) {
            evt.preventDefault();
            AppService.login({
              onSuccess: function() {
                $state.go(toState.name, toParams);
                AppService.showNotePopup();
              }
            });
          }
          break;
        case 'external':
          if(/nono-app/.test(toParams.link)) {
            evt.preventDefault();
            $timeout(function() {
              $location.path(decodeURIComponent(toParams.link.split('#')[1]));
            },500);
          }
          break;
      }
    });

    $rootScope.$on('$stateChangeSuccess', function() {
      // BI Log, page view event start
      BILog.enter();
    });

    // init bridge
    BridgeService.init();

    // init UserService
    UserService.init();

    $log.debug('runBlock end');
  }

})();

